.header {
    background-image: linear-gradient(
        rgba(34, 34, 34, 1),
        rgba(34, 34, 34, 0.7)
      ), url('../../../img/stormtroopers-2.webp');
    height: 95rem;
    width: 100%;
    background-size: cover;
    background-position: center;
    border-bottom: 2px solid var(--primary-color);
    position: relative;
}

.hero{
    max-width: 130rem;
    margin: 0 auto;
    padding: 0 3.2rem;
}


.section-hero{
    padding: 12.8rem 0 9.6rem 0;
}

.hero-description{
    color: #FFF;
    font-size: 2rem;
    line-height: 1.8;
    margin-bottom: 4.8rem;
}

.btnFull{
    color: black;
}